// fetch teasers from mock data and only return required object
export const fetchTeasers = (client, endpoint) => {
  return client
    .get(endpoint)
    .then(
      res =>
        res.data.teasers.map(teaser => ({
          id: teaser.id || undefined,
          title: teaser.title || undefined,
          image: teaser.image || undefined,
          source: teaser.source || undefined
        })) || undefined
    )
    .catch(err => {
      console.error(err);
    });
};



