import React, { Component } from 'react';
import axios from 'axios';
import { fetchTeasers } from '../../actions/teasers';
import Teaser from '../../components/Teaser';
import { StyledSection } from './styles';

export default class TeaserSection extends Component {
  constructor(props) {
    super(props);

    this.state = {
      teasers: []
    };
  }

  componentWillMount() {
    // fetch mock data and set
    fetchTeasers(axios, '/data.json').then(teasers =>
      this.setState({
        teasers
      })
    );
  }

  render() {
    const { teasers } = this.state;

    return (
      <StyledSection>
        {teasers.map((teaser, index) => <Teaser key={index} teaser={teaser} />)}
      </StyledSection>
    );
  }
}



