import React from 'react';
import {
  TeaserContainer,
  TeaserImageContainer,
  TeaserTitleContainer,
  Logo,
  Title
} from './styles';

const Teaser = ({ teaser }) => {
  const { image, title, source } = teaser;

  return (
    <TeaserContainer data-test="teaser-container">
      {image !== undefined && <TeaserImage image={image} title={title} />}
      {title !== undefined && <TeaserTitle source={source} title={title} />}
    </TeaserContainer>
  );
};

export default Teaser;

const TeaserImage = ({ image, title }) => (
  <TeaserImageContainer>
    <img src={image} alt={title} />
  </TeaserImageContainer>
);

const TeaserTitle = ({ source, title }) => {
  const { logo, name } = source;

  return (
    <TeaserTitleContainer>
      {source !== undefined && (
        <Logo>
          <img src={logo} alt={name} />
        </Logo>
      )}
      <Title>{title}</Title>
    </TeaserTitleContainer>
  );
};



