import styled from 'styled-components';

export const TeaserContainer = styled.div.attrs({
  [`data-test`]: 'teaser-container'
})`
  position: relative;
`;

export const TeaserImageContainer = styled.div`
  img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
`;

export const TeaserTitleContainer = styled.div`
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  display: flex;
  align-items: flex-start;
  padding: 5px 10px;
  position: absolute;
  bottom: 0;
  left: 0;
  min-height: 65px;
  width: 100%;
`;

export const Logo = styled.div`
  height: 40px;
  width: 40px;
  margin: 5px 10px 0 0;

  img {
    object-fit: contain;
    height: 100%;
  }
`;

export const Title = styled.div`
  padding: 10px 0;
`;



