import React, { Component } from 'react';
import styled from 'styled-components';

import TeaserSection from './containers/TeaserSection';

class App extends Component {
  render() {
    return (
      <AppContainer data-test="app">
        <TeaserSection />
      </AppContainer>
    );
  }
}

export default App;

const AppContainer = styled.div`
  background: #000;
`;



